@font-face {
    font-family: 'gothampro';
    font-weight: normal; 
    font-style: normal;

    src: url('../fonts/GothamPro.ttf');
}

@font-face {
    font-family: 'gothampro-bold';
    font-weight: normal; 
    font-style: normal;

    src: url('../fonts/GothamPro-Bold.ttf');
}

@font-face {
    font-family: 'gothampro-black';
    font-weight: normal; 
    font-style: normal;

    src: url('../fonts/GothamPro-Black.ttf');
}

@font-face {
    font-family: 'gothampro-medium';
    font-weight: normal; 
    font-style: normal;

    src: url('../fonts/GothamPro-Medium.ttf');
}

@font-face {
    font-family: 'geometria';
    font-weight: normal; 
    font-style: normal;

    src: url('../fonts/Geometria.woff');
}

*:focus {
    outline: none;
}

body {
    position: relative;

    overflow-x: hidden;

    margin: 0;
    padding: 0;

    font-family: gothampro, serif;
    font-size: 16px;
    font-weight: 400;
    line-height: normal;

    opacity: 1;
    color: #003b18;
    background-color: #fff; 
    background-image: url(../img/bg.png);
    background-repeat: no-repeat;
    background-size: cover;
}

section,
header,
footer {
    position: relative;

    background-position: center; 
    background-size: cover;
}

.header {
    z-index: 1000;

    width: 100%;
    margin-top: 25px; 

    font-family: gothampro-bold;
}
.header__dop {
    background-color: #2c0804;
}
.header__inner {
    display: flex;

    padding: 10px 30px 10px 50px;

    background-color: #fff; 

    justify-content: space-between;
    align-items: center;
}
.header-menu {
    display: flex;

    margin: 0;
    padding: 0; 

    list-style-type: none;

    font-family: gothampro-bold;
    font-size: 11px;

    text-transform: uppercase;

    justify-content: space-between;
}
.header-menu li {
    display: list-item;

    text-align: -webkit-match-parent;
}
.header-menu li a {
    display: block;

    font-size: 11px; 

    color: #003a18;
    border-bottom: 1px solid #003a18;

    text-transform: uppercase;
}
.header-menu li a:hover {
    text-decoration: none;
}
.header-menu li:not(:last-child) {
    margin-right: 50px;
}
.header-right {
    display: flex;
    flex-direction: column;

    align-items: flex-end;
}
.header-right__number {
    font-size: 24px; 

    color: #47b947;
}
.header-right__link {
    margin-top: 5px; 

    font-size: 11px;

    color: #003a18;
    border-bottom: 1px solid #003a18;

    text-transform: uppercase;
}
.header-right__link:hover {
    color: #003a18;

    text-decoration: none;
}
.header__services {
    display: flex;

    padding: 0;

    list-style-type: none;

    background: linear-gradient(90deg, #5fc147, #47a968);

    justify-content: space-between;
}
.header__services li {
    position: relative;
}
.header__services li:not(:last-child) {
    border-right: 1px solid #85cd7c;
}
.header__service {
    display: block;

    width: 100%;
    padding: 22px 27.9px;

    font-size: 11px;

    color: #fff;

    transition: .3s; 
    text-transform: uppercase;
}
.header__service:hover {
    color: #fff;
    background-color: #85cd7c; 

    text-decoration: none;
}

.m-header {
    display: none;

    background-color: #fff;
}

.calc {
    padding: 70px 65px; 

    text-transform: uppercase;
}
.calc .col-12 {
    padding-left: 65px;
}
.calc__title {
    font-size: 72px;

    color: #47b947;
}
.calc__subtitle {
    margin-top: -20px;
    margin-bottom: 65px;
    margin-left: 3px; 

    font-size: 30px;

    color: #003a18;
}
.calc__box {
    display: flex;
    flex-direction: column;

    max-width: 670px;
}
.calc__line1 {
    padding: 26px 40px; 

    color: #003a18;
    background-color: #fff;
}
.calc__text1 {
    font-size: 24px;
}
.calc__text2 {
    font-size: 14px;
}
.calc__line2 {
    display: flex;

    padding: 20px 40px 30px; 

    background-color: #f9f9f9;

    align-items: flex-end;
}
.calc__inputbox {
    display: flex;
    flex-direction: column;

    font-size: 11px;

    color: #003a18;
}
.calc__inputbox span {
    margin-bottom: 10px;
    margin-left: 15px;
}
.calc__select {
    position: relative;

    width: 175px;
    padding: 10px;

    color: #003a18;
    border: none; 

    text-transform: uppercase;
}
.calc__selectbox {
    position: relative;

    margin-right: 17px;
}
.calc__selectbox:before {
    position: absolute;
    bottom: -3px;
    left: -3px;

    width: 181px;
    height: 40px;

    background: #84cd47;
    background: linear-gradient(120deg, #84cd47 38%, #478faf 74%); 

    content: '';
}
.calc__number {
    position: relative; 

    display: flex;

    width: 81px;
    margin-bottom: -3px;
    padding: 8px 0;

    color: #fff;
    background: #84cd47;
    background: linear-gradient(120deg, #84cd47 38%, #478faf 74%);

    justify-content: center;
}
.calc__number .fieldCount {
    width: 48px; 

    font-size: 18px;

    color: #fff;
    border: none;
    background-color: transparent;

    text-align: center;
}
.calc__number .but {
    border: none; 
    background-color: transparent;
}
.calc__number .dec {
    position: absolute;
    top: 0; 
    left: 0;

    width: 30px;
    height: 39px;

    background-image: url(../img/arrow-left.png);
    background-repeat: no-repeat;
    background-position: center center;

    cursor: pointer;
}
.calc__number .inc {
    position: absolute;
    top: 0; 
    right: 0;

    width: 30px;
    height: 39px;

    background-image: url(../img/arrow-right.png);
    background-repeat: no-repeat;
    background-position: center center;

    cursor: pointer;
}
.calc__numberbox span {
    margin-left: 0;
}
.calc__line3 {
    padding: 26px 40px; 

    color: #003a18;
    background-color: #fff;
}
.calc__line3-inner {
    display: flex;

    width: 465px; 

    justify-content: space-between;
}
.calc__total-text {
    font-size: 11px;
}
.calc__total {
    font-family: gothampro-black; 
    font-size: 36px;
}
.calc__btn {
    padding: 15px 35px;

    font-size: 11px;

    color: #003a18;
    border: none;

    cursor: pointer; 
    text-transform: uppercase;
}
.calc__pic {
    position: absolute;
    right: 6px;
}

.trust {
    padding-top: 100px; 

    background-image: url(../img/trust.png);
    background-repeat: no-repeat;
    background-position: 0 0;
    background-size: cover;
}
.trust__title {
    margin-bottom: 100px;
    margin-left: 65px;
}
.trust__text {
    max-width: 600px;
    margin-bottom: 30px; 
    margin-left: 65px;

    font-size: 18px;
}
.trust__inner {
    position: relative;
}
.trust__box {
    width: 660px;
    margin-bottom: 60px; 
    margin-left: 28px;
    padding: 25px 35px;

    border: 1px dashed #a4da9c;
}
.trust__box-title {
    margin-bottom: 35px; 

    font-size: 24px;
}
.trust__box-item {
    display: flex;

    margin-right: 9px; 
    padding: 15px 23px;

    font-family: gothampro-bold;

    color: #0a3e1f;
    border: 1px solid #62be5a;

    align-items: center;
}
.trust__box-pic {
    margin-right: 15px;
}
.trust__box-line1 {
    display: flex;

    margin-bottom: 10px;
}
.trust__box-line2 {
    display: flex;
}
.trust__box-line2 .trust__box-pic {
    margin-right: 20px;
}
.trust__img {
    position: absolute;
    right: -104px;
    bottom: -60px;
}

.reason {
    padding: 40px 65px; 

    background-image: url(../img/reason.png);
    background-repeat: no-repeat;
    background-size: cover;
}
.reason__inner {
    position: relative;
}
.reason__title {
    display: flex;

    margin-bottom: 60px;
    margin-left: 65px; 

    align-items: center;
}
.reason__title-number {
    margin-right: 20px; 

    font-family: gothampro-black;
    font-size: 74px;

    color: #51d631;
}
.reason__title-text {
    display: flex;
    flex-direction: column;
}
.reason__title-line1 {
    font-family: gothampro-medium;
    font-size: 36px;
}
.reason__title-line2 {
    font-size: 18px;
}
.reason__items {
    display: flex;

    margin-left: 32px;
}
.reason__item {
    display: flex;
    flex-direction: column; 

    padding: 40px 25px 60px 35px;
}
.reason__item:first-child {
    border-right: 1px solid #386149;
}
.reason__mobile {
    display: none;
}
.reason__name {
    display: flex;

    margin-bottom: 30px; 

    align-items: center;
}
.reason__number {
    margin-right: 20px; 

    font-family: gothampro-black;
    font-size: 60px;

    color: #51d631;
}
.reason__nametext {
    font-family: gothampro-bold;
    font-size: 24px;
}
.reason__text {
    max-width: 300px;
}
.reason__dop {
    position: absolute;
    z-index: 1;
    right: -104px;
    bottom: -632px;

    width: 524px;
    padding-top: 110px; 

    background-image: url(../img/dop.png);
    background-repeat: no-repeat;
    background-size: cover;
}
.reason__dop-name {
    display: flex;

    margin-bottom: 30px;
    margin-left: 113px; 

    align-items: center;
}
.reason__dop-number {
    margin-right: 20px; 

    font-family: gothampro-black;
    font-size: 112px;
}
.reason__dop-nametext {
    font-family: gothampro-bold;
    font-size: 24px;
}
.reason__dop-text {
    max-width: 320px;
    margin-bottom: 45px;
    margin-left: 113px; 

    opacity: .5;
}
.reason__dop-btn {
    margin-bottom: 107px;
    margin-left: 190px; 

    color: #0c652f;
    background-color: #fff;
}

.reviews {
    padding: 90px 0 57px; 

    background-color: #f4f4f4;
}
.reviews__title {
    margin-left: 65px; 

    color: #0b652f;
}
.reviews__subtitle {
    margin-bottom: 65px;
    margin-left: 65px; 

    font-size: 18px;

    color: #0b652f;
}
.reviews__item {
    width: 313px;
    margin: 0 auto; 
    padding: 36px 25px;

    background-color: #fff;

    text-align: center;
}
.reviews__photo {
    width: 80px !important;
    height: 80px;
    margin: 0 auto 28px; 

    border-radius: 100%;
}
.reviews__name {
    margin-bottom: 10px; 

    font-family: gothampro-bold;
    font-size: 18px;

    color: #0b652f;
}
.reviews__job {
    margin-bottom: 30px; 

    font-size: 14px;

    color: black;
}
.reviews__line {
    width: 67px;
    height: 1px;
    margin: 0 auto 25px; 

    background-color: #4db541;
}
.reviews__text {
    color: #4b4b4b;

    text-align: left;
}
.reviews .owl-prev,
.reviews .owl-next {
    position: absolute;
    top: 150px;

    width: 61px;
    height: 61px;

    background-color: #0c6730;
}
.reviews .owl-prev {
    left: -66px; 

    background-image: url(../img/left.png);
    background-repeat: no-repeat;
    background-position: center center;
}
.reviews .owl-next {
    right: -66px; 

    background-image: url(../img/right.png);
    background-repeat: no-repeat;
    background-position: center center;
}

.breadcrumbs__items {
    display: flex;

    padding-bottom: 30px;
}

.breadcrumbs__item {
    padding: 20px;

    font-size: 10px; 

    text-transform: uppercase;
}

.breadcrumbs__item:first-child {
    position: relative; 

    background-color: #003b18;
}
.breadcrumbs__item:first-child a {
    color: #fff;
}
.breadcrumbs__item:first-child a:hover {
    color: #fff;
}

.breadcrumbs__item:first-child:after {
    position: absolute;
    z-index: 1;
    top: 0;
    right: -11px;

    width: 0;
    height: 0;

    border-width: 25.5px 0 25.5px 11px;
    border-style: solid;
    border-color: transparent transparent transparent #003b18; 

    content: '';
}

.breadcrumbs__item:nth-child(2),
.breadcrumbs__item:nth-child(3) {
    position: relative; 

    background-color: #fff;
}
.breadcrumbs__item:nth-child(2) a,
.breadcrumbs__item:nth-child(2) span,
.breadcrumbs__item:nth-child(3) a,
.breadcrumbs__item:nth-child(3) span {
    color: #003b18;
}
.breadcrumbs__item:nth-child(2) a:hover,
.breadcrumbs__item:nth-child(3) a:hover {
    color: #003b18;
}

.breadcrumbs__item:nth-child(3):before {
    position: absolute;
    top: 23px; 
    left: 0;

    width: 5px;
    height: 5px;

    background-image: url(../img/bread.png);
    background-repeat: no-repeat;
    background-position: center center;

    content: '';
}

.breadcrumbs__title {
    margin-bottom: 50px; 

    font-size: 24px;

    color: #003b18;

    text-transform: uppercase;
}

.news-list__inner {
    background-color: #fff;
}

.news-list__item {
    margin-bottom: 30px;
}

.news-list__title {
    padding: 30px 35px;

    font-size: 14px;

    color: black;
}

.news-list__img {
    width: 100%; 
    height: 249px;
}

.news-list__text {
    padding: 35px;

    font-size: 14px;

    color: black;
}

.news-list__btn {
    color: #fff; 
    background-color: #003b18;
}

.news-list__bottom {
    display: flex;

    padding: 0 40px 50px; 

    justify-content: space-between;
    align-items: center;
}

.news-list__date {
    font-size: 14px; 

    color: black;
}

.news-list__dop {
    margin-top: 65px;
}

.news {
    margin-bottom: 60px;
}
.news__title {
    margin-bottom: 45px; 

    font-size: 24px;

    color: #003b18;
}
.news__date {
    margin-bottom: 40px;
}
.news__box {
    padding: 80px 95px;

    color: #003b18; 
    background-color: #fff;
}
.news__box p {
    line-height: 30px;
}
.news__box h1,
.news__box h2,
.news__box h3,
.news__box h4,
.news__box h5,
.news__box h6 {
    font-family: gothampro-bold;
}
.news__box img {
    max-width: 100%;
}

.title__value {
    margin-left: 95px; 

    font-size: 24px;

    color: #003b18;
}

.service__box {
    position: relative;

    display: flex;

    margin-bottom: 50px; 
    padding: 50px 115px 65px;

    background-color: #fff;
    background-image: url(../img/service-bg.png);
    background-repeat: no-repeat;
    background-position: 100% 100%;
    box-shadow: 0 0 10px rgba(0, 0, 0, .5);

    justify-content: flex-end;
}

.service__box:before {
    position: absolute;
    top: 0;
    left: 0;

    width: 247px;
    height: 100%;

    background-color: #d4ded8; 

    content: '';
}

.service__img {
    position: absolute;
    top: 112px;
    left: 114px;

    max-width: 300px;
}

.service__info {
    max-width: 540px;

    color: #003b18;
}

.service__title {
    margin-bottom: 40px; 

    font-size: 36px;

    text-transform: uppercase;
}

.service__bottom {
    display: flex;

    margin-top: 40px; 

    align-items: center;
}

.service__btn {
    margin-right: 25px; 

    color: #fff;
    background-color: #003b18;
}

.service__price {
    font-size: 24px;
}
.service__price-title {
    margin-bottom: 30px; 

    font-size: 24px;

    text-transform: uppercase;
}

.table {
    font-family: gothampro-bold;

    box-shadow: 0 0 10px rgba(0, 0, 0, .5); 

    text-align: center;
}
.table__container {
    overflow-y: auto;

    width: 100%;
    margin-bottom: 70px; 

    _overflow: auto;
}
.table__container::-webkit-scrollbar {
    width: 14px;
    height: 14px; 

    -webkit-appearance: none;
}
.table__container::-webkit-scrollbar-thumb {
    border: 3px solid #fff;
    border-radius: 8px;
    background-color: rgba(0, 0, 0, .3);
}
.table thead {
    font-size: 11px;

    color: #fff; 
    background-color: #003b18;

    text-transform: uppercase;
}
.table thead td {
    min-width: 100px; 
    padding: 20px 0;
}
.table thead td:not(:last-child) {
    border-right: 1px solid #215436;
}
.table tbody tr {
    font-size: 14px; 

    background-color: #fff;
}
.table tbody td {
    padding: 20px 0; 

    border: none;
}
.table tbody tr:nth-child(2n) {
    background-color: #f0f0f0;
}
.table tbody a {
    font-size: 11px;

    color: #003b18; 
    border-bottom: 1px solid #003b18;

    text-transform: uppercase;
}
.table tbody a:hover {
    color: #003b18;
    border-bottom: none;

    text-decoration: none;
}

.footer {
    background-color: #06371a;
}
.footer__col1,
.footer__col2,
.footer__col3 {
    padding: 0;
}
.footer__col1 {
    display: flex;
    flex-direction: column;

    padding: 30px 0 40px; 

    justify-content: space-between;
    align-items: center;
}
.footer__text {
    max-width: 270px; 

    opacity: .5;

    text-align: left;
}
.footer__copyright {
    font-family: gothampro-bold;
    font-size: 10px; 

    text-transform: uppercase;
}
.footer__first {
    padding: 65px;

    font-size: 16px; 

    opacity: .5;
    color: #fff;
}
.footer__menu {
    display: flex;
    flex-direction: column;

    width: 100%;
    margin-bottom: 0; 
    padding: 0;

    list-style-type: none;

    font-family: gothampro-bold;

    border-right: 1px solid #265138;
    border-left: 1px solid #265138;

    text-align: center;
}
.footer__menu-item {
    display: block;

    width: 100%;
    padding: 25px 0;

    font-size: 14px;

    color: #fff;

    transition: .3s;
}
.footer__menu-item span {
    border-bottom: 1px solid #fff;
}
.footer__menu-item:hover {
    color: #fff;
    background-color: #265138; 

    text-decoration: none;
}
.footer__menu li:not(:last-child) {
    border-bottom: 1px solid #265138;
}
.footer__menu li:first-child {
    border-bottom: 0;
}
.footer__menu li:nth-child(2) {
    border-top: 1px solid #265138;
}
.footer__col3 {
    display: flex;
    flex-direction: column;

    padding-top: 85px;

    text-align: center;

    align-items: center;
}
.footer__phone {
    margin-bottom: 80px; 

    font-family: gothampro-bold;
    font-size: 18px;
}
.footer__socicons {
    display: flex;
    flex-direction: column;

    padding: 25px 40px;

    border: 1px solid #446953;
}
.footer__socicons span {
    margin-bottom: 15px;

    font-family: gothampro-bold;
    font-size: 12px;

    text-transform: uppercase;
}
.footer__soclinks {
    display: flex;

    justify-content: center;
    align-items: center;
}
.footer__soclink:not(:last-child) {
    margin-right: 20px;
}
.footer__info {
    max-width: 180px;
    margin-top: 60px; 

    font-family: gothampro;
    font-size: 12px;

    opacity: .5;
}
.footer__studio {
    margin-top: 20px;
}
.footer__studio a {
    display: block;

    padding: 10px 30px;

    color: #fff;
    border: 1px solid #446953;

    transition: .3s;
}
.footer__studio a:hover {
    background-color: #446953;

    text-decoration: none;
}
.footer__studio-line1 {
    margin-bottom: 2px; 

    font-size: 14px;
}
.footer__studio-line2 {
    font-family: gothampro-bold; 
    font-size: 18px;
}

section {
    display: flex;
    flex-direction: column;
}

.btn {
    padding: 15px 35px;

    font-family: gothampro-bold;
    font-size: 11px;

    border: none;
    border-radius: 0; 

    cursor: pointer;
    text-transform: uppercase;
}

.orange {
    color: #f05e00;
}

.gradient {
    background: #84cd47;
    background: linear-gradient(120deg, #84cd47 38%, #478faf 74%);
}

.gb {
    font-family: gothampro-bold;
}

.h2 {
    font-family: gothampro-medium; 
    font-size: 60px;
}

button:hover,
button:active,
button:focus {
    outline: none;
}

.input-error {
    border: 1px solid red;
}

.order__quantity-price1--hidden {
    display: none;
}

@media only screen and (max-width: 1200px) {
    .header-menu li:not(:last-child) {
        margin-right: 30px;
    }
    .header__service {
        padding: 22px 12.9px;
    }
    .calc__pic {
        display: none;
    }
    .reason__dop {
        display: none;
    }
    .reason__mobile {
        display: block;
    }
    .reason__item:nth-child(2) {
        border-right: 1px solid #386149;
    }
    .reason__dop-btn {
        margin-top: 20px;
        margin-bottom: 0; 
        margin-left: 0;
    }
    .trust__img {
        display: none;
    }
    .service__img {
