@media only screen and (max-width: 992px) {
    .m-header {
        display: block;

        border-bottom: 1px solid #2b3c4b;
    }
    .m-header .hamburger-inner,
    .m-header .hamburger-inner::after,
    .m-header .hamburger-inner::before {
        background-color: #47a968;
    }
    .m-header .container-fluid {
        display: block;

        text-align: right;
    }
    .m-header__menu {
