        text-align: left;
    }
    .m-header__menu li {
        margin-bottom: 5px;
    }
    .m-header__menu a {
        color: #003a18;
    }
    .header {
        display: none;
    }
    .reason {
        padding: 40px 0;
    }
    .reason__title {
        margin-bottom: 0;
        margin-left: 0; 

        justify-content: center;
    }
    .reason__items {
        flex-direction: column;
    }
    .reason__item {
        display: flex;

        padding: 20px 10px; 

        border: none !important;

        align-items: center;
    }
    .reviews {
        padding: 90px 0 140px;
    }
    .reviews .owl-next,
    .reviews .owl-prev {
        top: auto;
        bottom: -100px;
    }
    .reviews .owl-prev {
        left: calc(50% - 100px);
    }
    .reviews .owl-next {
        right: calc(50% - 100px);
    }
    .service__img {
